html {
  box-sizing: border-box;
  font-size: 100%; }

*, *:before, *:after {
  box-sizing: inherit; }

* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none; }

.wrapper-inner {
  max-width: 1170px;
  margin: 0 auto; }

/* ----------------- Media queris
----------------------------------------------------------------------- */
h1 {
  font-size: 50px; }

h2 {
  font-size: 40px; }

h3 {
  font-size: 30px; }

.svg__icon {
  display: inline-block;
  width: 50px;
  height: 50px;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor; }

.svg__icon--color {
  fill: #bebebe; }

@font-face {
  font-family: 'ns';
  src: url("./../src/fonts/NeoSansStd-Bold/NeoSansStd-Bold.eot?#iefix") format("embedded-opentype"), url("./../src/fonts/NeoSansStd-Bold/NeoSansStd-Bold.ttf") format("truetype"), url("./../src/fonts/NeoSansStd-Bold/NeoSansStd-Bold.otf") format("opentype"), url("./../src/fonts/NeoSansStd-Bold/NeoSansStd-Bold.woff") format("woff"), url("./../src/fonts/NeoSansStd-Bold/NeoSansStd-Bold.svg#NeoSansStd-Bold") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block; }

.header-box {
  grid-area: header-box;
  position: relative;
  z-index: 5; }
  .header-box .header-box__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0; }
    .header-box .header-box__content .header-logo a {
      display: flex; }
      .header-box .header-box__content .header-logo a img {
        max-width: 100%;
        height: auto; }
    .header-box .header-box__content nav.header-nav ul {
      display: flex; }
      .header-box .header-box__content nav.header-nav ul li a {
        display: flex;
        font-size: 20px;
        padding: 10px;
        color: #cecece; }

.logo {
  width: 5em;
  height: 5em;
  animation: bounceIn 0.6s;
  transform: rotate(0deg) scale(1) translateZ(0);
  transition: all 0.4s cubic-bezier(0.8, 1.8, 0.75, 0.75);
  cursor: pointer; }

.logo:hover {
  transform: rotate(10deg) scale(1.1); }

.bubble {
  fill: #e76853; }

.icon {
  fill: #fff; }

@keyframes bounceIn {
  0% {
    opacity: 1;
    transform: scale(0.3); }
  50% {
    opacity: 1;
    transform: scale(1.05); }
  70% {
    opacity: 1;
    transform: scale(0.9); }
  100% {
    opacity: 1;
    transform: scale(1); } }

html,
body {
  height: 100%;
  height: 100%; }

body {
  position: relative;
  font-family: "Roboto" sans-serif; }

.grid-container {
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas: "header-box header-box header-box header-box"  "banner-box banner-box banner-box banner-box" "banner-box banner-box banner-box banner-box" "banner-box banner-box banner-box banner-box"  "arrow-down-box arrow-down-box arrow-down-box arrow-down-box";
  background: url("./../src/img/laptop-glasses.jpeg") center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  z-index: 4; }
  .grid-container::after {
    content: "";
    background-color: rgba(12, 12, 12, 0.6);
    position: absolute;
    z-index: 4;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }

.banner-box {
  grid-area: banner-box;
  display: flex;
  justify-content: center;
  align-items: center; }
  .banner-box .wrapper-inner .banner-box__content {
    flex-direction: column;
    display: flex;
    position: relative;
    z-index: 5;
    color: #ffffff; }
    .banner-box .wrapper-inner .banner-box__content .cta {
      background: #fff;
      color: #000;
      text-decoration: none;
      font-size: 30px;
      text-transform: uppercase;
      padding: .4em;
      transition: all .1s ease-in-out;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: ns, Montserrat, sans-serif;
      overflow: hidden; }
      .banner-box .wrapper-inner .banner-box__content .cta .svg__icon--color {
        fill: #000;
        padding: 0 0 0 10px; }
      .banner-box .wrapper-inner .banner-box__content .cta:hover {
        color: #155799;
        background: #000; }
        .banner-box .wrapper-inner .banner-box__content .cta:hover:hover .svg__icon--color {
          fill: #155799; }
    .banner-box .wrapper-inner .banner-box__content h1 {
      font-family: ns,Roboto,sans-serif;
      text-transform: uppercase;
      font-size: 60px;
      order: -1;
      padding: 0 0 2px; }
      .banner-box .wrapper-inner .banner-box__content h1 span {
        color: #eee;
        display: inline-block;
        background: #000;
        margin-bottom: 5px;
        padding: 0 .2em;
        transition: all .1s ease-in-out; }
  .banner-box .wrapper-inner .banner-box__content .cta:hover ~ h1 span {
    color: #000;
    background: #fff; }

.arrow-down-box {
  grid-area: arrow-down-box;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 5;
  bottom: 0; }

.about-box .about-box__content,
.about-box .wrraper-inner {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px; }

.gallery-box {
  padding: 20px;
  background-color: springgreen; }
